	import axios from "axios"
	import aliMit from "../chat/api/aliwsdk"
	export default {
		props: {
			id: {
				type: String
			}
		},
		data() {
			const userName = window.localStorage.getItem("userName")
			const uid = window.localStorage.getItem('uid')
			const appkey = window.localStorage.getItem('appkey')
			const credential = window.localStorage.getItem('credential')
			const friendTouid = window.localStorage.getItem('friendTouid')
			const myId = window.localStorage.getItem("myId")
			const friendNick = window.localStorage.getItem("friendNick")
			const myTouid = this.$route.params.id
			return {
				userName,
				uid,
				appkey,
				credential,
				friendTouid,
				myTouid,
				myId,
				friendNick,
				avatar: '',
				toAvatar: ''
			}
		},
		components: {
			aliMit
		},
		mounted() {

			// location.reload()
			let nextkey = ""
			const myTouid = this.myTouid.toString()
			const myId = this.myId
			window.localStorage.setItem("chatId", this.myTouid)

			// aliLoginH5()
			document.getElementById("mainnews2").classList.remove("is-hide");
			if(!window.hasWKIT) {
				window.hasWKIT = true
				// if (window.friendTouid !== this.friendTouid) {
				// 	window.friendTouid = this.friendTouid

				WKIT.init({
					container: document.getElementById('mainnews2'),
					uid: this.uid,
					appkey: this.appkey,
					credential: this.credential,
					touid: this.friendTouid,
					// imgUploader:false, 是否可以上传图片
					//avatar: avatar, 登录用户的头像
					//toAvatar: toAvatar, 对方用户的头像
					themeBgColor: '#888', // 主题背景色，示例: #f60, #dc2a2e
					themeColor: '#FC3768', // 主题文字颜色 默认值: #fff
					msgBgColor: '#FC3768', // 消息背景色 默认值: themeBgColor
					msgColor: '#fff', // 消息文字颜色 默认值: #4a4a4a
					titleBar: false, // 是否显示顶部title条, PC端设置无效 默认值: true
					onLoginSuccess: this.onLoginSuccess, // 登录成功后的回调
					onLoginError: this.onLoginError, // 登录失败后的回调
					onMsgReceived: this.onMsgReceived, // 收到消息后的回调
					onMsgSent: this.onMsgSent, // 消息发送后的回调
					// beforeImageUploaderTrigger:     [Function]      // 接管上传图片按钮的点击
					// onUploaderSuccess:              [Function]      // 上传图片成功回调, 返回图片地址
					// onUploaderError                 [Function]      // 上传图片失败回调, 返回上传错误原因
					onBack: this.onBack // 移动端点击返回的回调 默认值: false
					// onAudioReceived:            [Function]      // 当收到语音消息时，可以通过这个回调，修改消息中显示的DOM，默认语音消息不展示，详见示例10
				});
				setTimeout(function() {
//					var demo = document.getElementById('mainnews2');
//					demo.style.width = '500px';
//					demo.style.height = '300px';
					WKIT.resize();
				}, 3000);

			} else {

			}
		},
		methods: {

			onLoginSuccess() {
				console.log('登录成功')
			},
			onLoginError() {
				console.log('登录失败')
			},
			onMsgReceived() {
				console.log('收到消息')
			},
			onMsgSent() {
				console.log('消息发送')
			},
			onBack() {
				document.getElementById("mainnews2").classList.add("is-hide");
				//				this.$router.replace({path:'/message'})
				//				window.opener.document.location.reload()
				//				location.replace('/message');
				this.$router.replace('/message');
				/* setTimeout(location.reload(),6000)
				 location.reload(); */
			}
		}
	}



